import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, MenuController, Platform } from 'ionic-angular';
import { MarketProvider } from '../../providers/market/market';
import { ToolsProvider } from '../../providers/tools/tools';
import { LoginProvider } from '../../providers/login/login';

@IonicPage()
@Component({
  selector: 'page-admin',
  templateUrl: 'admin.html',
})
export class AdminPage {
  private tanggal: Date;
  private bln: string[];
  private dataJual: any[];
  private tglSrc: any;
  private vld:boolean = true;

  private clientId: any;
  private ipAdr:any;

  constructor(
    public menu: MenuController ,
    public navCtrl: NavController, 
    public navParams: NavParams, 
    public mkt: MarketProvider, 
    public tool: ToolsProvider, 
    public lgn: LoginProvider,
    public platform: Platform
  ) {
    this.bln = ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep","Oct","Nov","Dec"];
    this.vld = true;
  }

  ionViewDidLoad() {
    this.platform.ready().then(()=>{
      this.menu.enable(true);      
      this.checkLogin();
    })
  }

  checkLogin(){
    console.log('check login');
    this.tool.getStg('login').then(
      (data)=>{
        if(!data || data.level!=='1'){ 
          this.navCtrl.setRoot('LoginPage');
        }else{
          this.clientId = data.clientId;
          this.tool.getStg('ip').then(
            (data)=>{
              if(data){
                this.ipAdr = data.ip;
                this.lgn.logger(this.clientId,'Membuka halaman Penjualan BBCA', data.ip).subscribe();
              }
            }
          );
        }
      },
      err=>{
        console.log('Home->getLoginStg()', err);
      }
    ); 
  }

  change(){
    let tgl: string;
    tgl = this.tanggal.toString();
    let dd = tgl.substr(8,2);
    let mm = tgl.substr(5,2);
    let yy = tgl.substr(2,2);
    this.vld = false;
    this.tglSrc = dd+' '+ this.bln[parseInt(mm)-1]+ " "+ yy
    this.mkt.getAdminData(this.tglSrc).subscribe(data=>{
      this.lgn.logger(this.clientId,'Mengambil data tanggal '+ this.tglSrc,this.ipAdr).subscribe();
      this.dataJual = data;
    })
  }

  getOddLot(){
    this.mkt.getOddLot(this.tglSrc);
    this.lgn.logger(this.clientId,'Mengunduh data OddLot tanggal '+ this.tglSrc,this.ipAdr).subscribe();
  }

  getFixLot(){
    this.mkt.getFixLot(this.tglSrc);
    this.lgn.logger(this.clientId,'Mengunduh data FixLot tanggal '+ this.tglSrc,this.ipAdr).subscribe();
  }
}



